<template>
  <div class="result-list center-center">
    <el-button type="primary" :loading="true"
      >正在验证支付结果，请勿关闭页面</el-button
    >
  </div>
</template>

<script>
import apiUser from "@/api/user";
import auth from "@/plugins/auth";
export default {
  name: "ResultView",
  data() {
    return {
      show: false,
    };
  },
  computed: {},
  methods: {
    async queryApplyInfo() {
      const trade_no = this.$route.query.trade_no;
      const [err] = await apiUser.vaidOrder({
        trade_no,
      });
      if (err) {
        this.$router.push(`/`);
        return;
      }
      if (trade_no) {
        this.$store.commit("setTradeNo", trade_no);
      }
      const buyId = auth.getToken("buyId");
      if (buyId) {
        this.$router.push(`/javs/${buyId}.html`);
      }
    },
  },
  created() {
    this.queryApplyInfo();
  },
};
</script>
<style scoped lang="scss">
.page-list {
  ::v-deep {
    .el-pager > li:last-child {
      display: none;
    }
  }
}
</style>
